.fr-callout {
  position: relative;
  padding: 1.5rem;
  --title-spacing: 0 0 0.5rem;
  --text-spacing: 0;
  margin: 0 0 1.5rem;
  /**
   * Inclusion de l'icône (optionnel)
   */
  /**
   * Ajout de marge quand on inclut un bouton (optionnel)
   */
}

@media (min-width: 48em) {
  .fr-callout {
    padding: 2rem 3rem;
  }
}

.fr-callout[class^="fr-icon-"]::before, .fr-callout[class*=" fr-icon-"]::before, .fr-callout[class^="fr-fi-"]::before, .fr-callout[class*=" fr-fi-"]::before {
  --icon-size: 1.5rem;
  display: block;
  margin: -0.5rem 0 0.5rem;
}

@media (min-width: 48em) {
  .fr-callout[class^="fr-icon-"]::before, .fr-callout[class*=" fr-icon-"]::before, .fr-callout[class^="fr-fi-"]::before, .fr-callout[class*=" fr-fi-"]::before {
    margin: -1rem 0 1rem -2rem;
  }
}

.fr-callout__title {
  font-weight:  700 ;
  font-size:  1.375rem ;
  line-height:  1.75rem ;
}

@media (min-width: 48em) {
  .fr-callout__title {
    font-size:  1.5rem ;
    line-height:  2rem ;
  }
}

.fr-callout__text {
  font-size:  1.125rem ;
  line-height:  1.75rem ;
}

.fr-callout .fr-btn {
  margin-top: 1rem;
}

